'use client';

import { useEffect } from 'react';
import { useRouter } from 'next/navigation';
import { useServerAuth } from '@/shared/hooks/useServerAuth';
import Layout from '@/shared/ui/components/Layout';
import Calendar from '@/services/reservations/ui/components/Calendar';

export default function CalendarPage() {
  const { isAuthenticated, loading } = useServerAuth();
  const router = useRouter();

  useEffect(() => {
    // Solo redirigir si ya termin√≥ de cargar Y no est√° autenticado
    if (!loading && !isAuthenticated) {
      console.log('‚ùå [CalendarPage] Usuario no autenticado, redirigiendo a login');
      router.push('/auth/login');
    }
  }, [isAuthenticated, loading, router]);

  if (loading) {
    return (
      <div className="flex min-h-screen items-center justify-center">
        <div className="text-center">
          <div className="loading-spinner-large"></div>
          <p>Cargando...</p>
        </div>
      </div>
    );
  }

  if (!isAuthenticated) {
    return null; // Se redirigir√° autom√°ticamente
  }

  return (
    <Layout>
      <Calendar />
    </Layout>
  );
} 